body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background: url(/static/media/textured-background.6a02881c.png);
  background-size: auto;
}

.App-header {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


.preview-thumbnail{
    max-width: 200px;
    max-height:200px;
}

.btn-div{
    padding: 20px;
    display: inline-block;
}

#all-btns-container{
    max-width:60vw;
}

.homepage-btn{
    border:none;
    background-color: rgba(64, 64, 64, 0.25);
    font-style: oblique;
    font-weight: bold
}
#options-form {
    color:rgb(0, 0, 0);
}

.centerDot {
    height:25px;
    width: 25px;
    border-radius: 50%;
    display: inline-block;
    background-color: blue;
}


.done-button{
    background-color: grey;
    position: fixed;
    bottom:3px;
}

#static-center-dot{
    height:30px;
    width:30px;
    border-radius: 50%;
    display: inline-block;
    position: fixed;
    top:50%;
    left:50%;
}

.static-extra-dot{
    height:30px;
    width:30px;
    border-radius: 50%;
    display: inline-block;
    position: fixed;
}

.opk-stripe{
    height:100%;
    width:10%;
    display: inline-block;
    position: fixed;
    top:0;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

#opk-exercise-div{
    height: 100vh;
    width: 100vw;
}

